import {NgModule} from '@angular/core';
import {PreloadAllModules, Routes, RouterModule,} from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/pl',
    pathMatch: 'full'
  },
  {
    path : ':lang',
    loadChildren: () => import('./mainPage/mainPage.module').then(m => m.MainPageModule)
  },
  {
    path: ':lang/legenda/:slug',
    loadChildren: () => import('./legenda/legenda.module').then(m => m.LegendaPageModule)
  },
  {
    path: ':lang/mapa',
    loadChildren: () => import('./mapa/mapa.module').then(m => m.MapaPageModule)
  },
  {
    path: ':lang/stronica/:slug',
    loadChildren: () => import('./stronica/stronica.module').then( m => m.StronicaPageModule)
  },
  {
    path: ':lang/ulubione',
    loadChildren: () => import('./ulubione-page/ulubione-page.module').then( m => m.UlubionePagePageModule)
  },
  {
    path: ':lang/roadmap',
    loadChildren: () => import('./roadmap-page/roadmap-page.module').then( m => m.RoadmapPagePageModule)
  },
  {
    path: ':lang/tag/:slug',
    loadChildren: () => import('./tag-page/tag-page.module').then( m => m.TagPagePageModule)
  },
  {
    path: ':lang/404',
    loadChildren: () => import('./error-page/error-page.module').then( m => m.ErrorPagePageModule)
  },
  {path: '**', redirectTo: '/404'},


        
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules, initialNavigation: 'enabledBlocking', useHash: true })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}