  getLegendPage() {
    if(this.settings.legends.length > 0) {
      this.settings.legendPage = this.settings.legends.find(obj => {
        return obj.Slug === this.settings.legendsPageSlug
      });
      if(!this.settings.legendPage) {
        this.router.navigate(['/' + this.settings.langCode + '/404'])
      } else {
        document.documentElement.style.setProperty(`--progress-width`, this.settings.legendPage.Progress);

        let seo_short = this.settings.legendPage.Content.replaceAll(/(\r\n|\n|\r)/gm, "");
        let seo_description = seo_short.match(/^.{1,150}/g);
        
        document.documentElement.setAttribute("lang", this.settings.langCode);
        this.titleService.setTitle(this.settings.legendPage.Title + ' • Baśnie z Mazur');
        this.meta.updateTag({ name: 'description', content: seo_description});
        // OpenGraph Meta Tags
        this.meta.updateTag({ property: 'og:title', content: this.settings.legendPage.Title});
        this.meta.updateTag({ property: 'og:description', content: seo_description});
        this.meta.updateTag({ property: 'og:image', content: this.settings.legendPage.FeaturedImage});
        this.meta.updateTag({ property: 'og:type', content: 'website'});
        this.meta.updateTag({ property: 'og:url', content: 'https://basniezmazur.pl'});
        // Twitter Meta Tags
        this.meta.updateTag({ property: 'twitter:title', content: this.settings.legendPage.Title});
        this.meta.updateTag({ property: 'twitter:description', content: seo_description});
        this.meta.updateTag({ property: 'twitter:image', content: this.settings.legendPage.FeaturedImage});
        this.meta.updateTag({ property: 'twitter:url', content: 'https://basniezmazur.pl'});


        // Schema Markup
        function addScript(attribute, text, callback) {
          var schema = document.createElement('script');
          for (var attr in attribute) {
            schema.setAttribute(attr, attribute[attr] ? attribute[attr] : null)
          }
          schema.innerHTML = text;
          schema.onload = callback;
          document.head.appendChild(schema);
        }

        addScript({
          type: 'application/ld+json',
        },
        '{"@context":"https://schema.org","@graph":[{"@type":"Article","datePublished":"'+ this.settings.legendPage.Date + '","headline":"' + this.settings.legendPage.Title + '","description":"' + seo_description + '","name":"' + this.settings.legendPage.Title + '","image":{"@type":"ImageObject","@id":"' + this.settings.legendPage.FeaturedImage + '","url":"' + this.settings.legendPage.FeaturedImage + '","height":670,"width":1200,"caption":"' + this.settings.legendPage.Title + '"},"thumbnailUrl":"' + this.settings.legendPage.FeaturedImage + '"}]}',
        function(){});
      }
    }
  }
  
  /*** GENERATE ACCESS TOKEN ***/
  getAccessToken() {
    var url = this.authUrl;
    let headers = new HttpHeaders({
      'Content-Type': 'application/json',
    });
    let content = {
      "grant_type": "refresh_token",
      "client_id": this.CLIENT_ID,
      "client_secret": this.CLIENT_SECRET,
      "refresh_token": this.REFRESH_TOKEN,
    }
      
    return new Promise ((resolve, reject) => {
      this.http.post(url, content, { headers }).subscribe(
        response => {
          resolve(response);
        },
        error => {
          resolve(error);
        }
      );
    }); 
  }

  /*** GET DATA - places, events, users ***/
  getData(key?: string) {
    var url = this.sheetApi + this.SHEET_ID + '/values/' + key;
    let headers = new HttpHeaders({
      'Authorization': `Bearer ${this.settings.token}`,
      'Accept': 'application/json',
    });
    
    return new Promise ((resolve, reject) => {
      this.http.get(url, { headers }).subscribe(
        response => {
          resolve(response);
        },
        error => {
          resolve(error);
        }
      );
    });
  }

  /*** CONVERT SHEET RESPONSE READABLE API ***/
  convertData(data) {
    const headers = data[0];
    const raw_data = data.slice(1,);
    let json = [];
    let filter = [];
    raw_data.forEach(d => {
        let object = {}
        for (let i = 0; i < headers.length; i++) {
          object[headers[i]] = d[i]
        }
        json.push(object)
    });
    return json;
  }
  

